import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CommonPaymentService {

  constructor(private http:HttpClient) { }

  getCountryName() {
    return this.http.get(`https://restcountries.com/v3.1/all`)
  }
}
